import * as React from 'react';
import { HoverToolbar } from './components/HoverToolbar';
import { IHoverToolbarPluginConfigs } from './types';
import { HoverCaptureContext } from './context/hoverCaptureContext';
import { HoverCapture } from './utils/hoverCapture';

export default function createRenderContent(
  hoverCapture: HoverCapture,
  configs: IHoverToolbarPluginConfigs,
) {
  const isDisabled = configs.isDisabled && configs.isDisabled();
  return function renderContent(_, controller, next) {
    return !isDisabled ? (
      <HoverCaptureContext.Provider value={hoverCapture}>
        {next()}
        <HoverToolbar controller={controller} configs={configs} />
      </HoverCaptureContext.Provider>
    ) : <>{ next() }</>;
  };
}
